import bottomTabBar from "@/components/bottomTabBar/bottomTabBar.vue"
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {uniNavBar,bottomTabBar},
	data() {
		return {
			isfirst: true,
			//个人信息,
			user: {
				username: '游客1002',
				face: '/static/img/face.jpg',
				signature: '点击昵称跳转登录',
				integral: 0,
				balance: 0,
				envelope: 0
			},
			// 订单类型
			orderList: [{ text: '全部订单', icon: 'fukuan' }, { text: '未付款', icon: 'fahuo' }, { text: '待提货', icon: 'shouhuo' }, { text: '已提货', icon: 'pingjia' }]
		};
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	onLoad() {
		this.statusHeight = 0;




	},
	onReady() {
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function() {},
			fail: function(e) {}
		});
	},
	onShow() {
		uni.getStorage({
			key: 'UserInfo',
			success: res => {
				if (!res.data) {
					if (this.isfirst) {
						//this.toLogin();
					}
					return;
				}
				this.user = res.data;
			},
			fail: e => {
				//this.toLogin();
			}
		});
	},
	methods: {
		toOrderList(index) {
			uni.setStorageSync('tbIndex', index);
			uni.navigateTo({ url: '../../user/order_list/order_list?tbIndex=' + index });
		},
		toShop() {
			uni.switchTab({
				url: '../../tabBar/home/home'
			})
			
		},
		toMyQR() {
			uni.navigateTo({
				url: '../../user/myQR/myQR'
			});
		},
		toLogin() {
			uni.showToast({ title: '请登录', icon: 'none' });
			uni.navigateTo({
				url: '../../login/login'
			});
			this.isfirst = false;
		},
		isLogin() {
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false;
		},
		toDeposit() {
			uni.navigateTo({
				url: '../../user/deposit/deposit'
			});
		}
	}
};
